<template>
  <div
    id="scroll"
    v-if="currentArr && currentArr.length > 0"
  >
    <div class='bgfff'>
      <div
        class="bgf1 mp g_flex g_fw"
        style="margin-top:0;"
      >
        <div class="w25 g_left">品类</div>
        <div class="w25">单价</div>
        <div
          v-if="$route.query.name != '保洁人工费'"
          class="w25"
        >消耗数量</div>
        <div class="w25 g_right">费用</div>
      </div>
      <div
        v-for="(item,i) in currentArr"
        :key="i"
        class="mp g_flex g_bb"
      >
        <div class="w25 g_left">
          {{ item.costName }}
        </div>
        <div class="c_8f w25">{{ item.price }}</div>
        <div
          v-if="$route.query.name != '保洁人工费'"
          class="c_8f w25"
        >x{{ item.num }}</div>
        <div class="w25 g_right">¥{{ $route.query.name == '保洁人工费'?Number(item.price).toFixed(2):Number(item.num*item.price).toFixed(2) }}</div>
      </div>
      <div class="g_flex f_r">
        <div>合计：<span class="c_fa">¥{{ Number(freePrice).toFixed(2) }}</span></div>
      </div>
    </div>
  </div>
  <div
    v-else
    class="mt50"
  >暂无数据</div>
</template>
<script>
import { GetBusinessOrderroomCostBillList } from '@/api/api_cleaning_or_goods';
import { GetBusinessCleanorderBillTimeByroom } from '@/api/api_staff';
export default {
    data() {
        return {
            currentArr: [],
            freePrice: null
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.name);
        if (this.$route.query.name != '保洁人工费') {
            this.getInfo();
        } else {
            this.getCleaning();
        }
    },
    methods: {
        getCleaning() {
            let data = {
                roomId: this.$route.query.roomId,
                billTime: this.$route.query.billTime
            };
            GetBusinessCleanorderBillTimeByroom(data).done((res) => {
                if (res.data) {
                    for (const k in res.data) {
                        let obj = {};
                        obj.price = res.data[k].amount;
                        obj.num = res.data[k].count;
                        obj.costName = k;
                        this.currentArr.push(obj);
                    }
                    this.currentArr.map((item) => {
                        this.freePrice += Number(item.price);
                    });
                }
            });
        },
        getInfo() {
            let data = {
                roomId: this.$route.query.roomId,
                billTime: this.$route.query.billTime,
                hotelCostType: this.$route.query.hotelCostType
            };
            GetBusinessOrderroomCostBillList(data).done((res) => {
                this.currentArr = res.data;
                if (this.currentArr && this.currentArr.length > 0) {
                    this.currentArr.map((item) => {
                        this.freePrice += Number(item.num * item.price);
                    });
                }
            });
        }
    }
};
</script>
<style scoped>
#scroll {
    position: fixed;
    left: 0;
    right: 0;
    overflow: auto;
    height: calc(100% - 70px);
    color: #000;
    font-size: 14px;
}
.bgfff {
    background: #fff;
    padding-top: 10px;
}
.bgf1 {
    background: #f1f1f1;
}
.mp {
    margin: 10px;
    padding: 12px;
}
.w25 {
    width: 25%;
}
.c_8f {
    color: #9b989b;
}
.f_r {
    font-size: 0.9rem;
    display: flex;
    justify-content: flex-end;
    font-weight: bold;
    padding: 14px 12px;
}
.c_fa {
    color: #fa5456;
}
.mt50 {
    margin-top: 50px;
    font-size: 14px;
}
</style>



